body {
    background-color: rgb(250, 250, 250);
}

.container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: white;
}

.image {
    width: 100%;
    height: 200px;
    margin: 0px;
}

.loading {
    height: calc(100vh - 200px);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
